/*footer-sections*/
.footer-section {
  background-color: #fff5f6;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-container {
  width: 87%;
}

.container-footer-title {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding-left: 20px;
  margin-left: 0px;
}

.footer-social {
  display: flex;
  justify-content: end;
}

.footer-title-1 {
  color: #ffa5ba;
  font-family: Titan One;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  display: flex;
}

.footer-title-2 {
  color: #d41443;
  font-family: Titan One;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 1.36px;
  text-transform: uppercase;
}

.container-footer-social {
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin-top: 40px;
  padding-right: 20px;
  margin-right: 0px;
}

.icon-footer-list {
  display: flex;
  flex-direction: row;
  gap: 28px;
  margin-top: 0px;
  margin-bottom: 0px;
}

.icon-footer-list li {
  width: 32px;
  height: 32px;
  background: #fea5bb;
  border-radius: 50%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.icon-footer-list li:hover {
  background-color: #d41443;
}

.footer-phone {
  display: flex;
  justify-content: end;
  margin-bottom: 40px;
  gap: 10px;
}

.footer-phone-text {
  color: #000;
  text-align: right;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 186%;
}

.footer-phone-number {
  color: #d41443;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 186%;
  text-decoration: none;
  transform: scale(1);
  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.footer-phone-number:hover {
  transform: scale(1.1);
}

.container-copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 16px;
  border-top: 1px solid #e1e1e1;
  padding-bottom: 4px;
  width: 100vw;
}

.text-copyright {
  color: #907e82;
  text-align: center;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 152%;
}

/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1157px) {
  /*footer-sections*/
  .footer-section {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .footer-container {
    max-width: 768px;
    display: flex;
    gap: 140px;
  }

  .container-footer-social {
    padding: 0px;
    width: 287px;
  }

  .container-footer-title {
    padding: 0px;
    width: 341px;
  }

  .footer-title-1 {
    font-size: 26px;
    letter-spacing: 1.04px;
  }

  .footer-title-2 {
    font-size: 48px;
    letter-spacing: 2.88px;
  }

  .icon-footer-list {
    margin-top: 60px;
  }

  .footer-phone-text {
    font-size: 14px;
    line-height: 186%;
    white-space: nowrap;
  }

  .footer-phone-number {
    font-size: 14px;
    line-height: 186%;
    white-space: nowrap;
  }

  .container-copyright {
    width: 100%;
  }

  .text-copyright {
    font-size: 14px;
    line-height: 186%;
    letter-spacing: 0.28px;
  }

  .br-for-mobile-2 {
    display: none;
  }
}

/* Desktop */
@media screen and (min-width: 1158px) {
  /*footer-sections*/
  .footer-section {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .footer-container {
    width: 1168px;
   
    
    display: flex;
    gap: 252px;
    justify-content:space-between;
      
  }

  .container-footer-social {
    padding: 0px;
   
  }

  .container-footer-title {
    margin-top: 40px;
    padding: 0px;
    
  }

  .footer-phone-text {
    font-size: 16px;
    line-height: 186%;
    letter-spacing: 0.32px;
  }

  .footer-phone-number {
    font-size: 16px;
    line-height: 186%;
    letter-spacing: 0.32px;
  }

  .container-copyright {
    width: 100%;
  }
}
