header {
  width: 100%;
  background: #ffb8ca;
  position: relative;
}

#background-wrap {
  z-index: 1;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.banner-container {
  background-color: #ffb8ca;
}

.splash-milk, .tasty-reaction, .article-banner, .header-section-desktop, .banner-section-desktop, .banner-section-tablet {
  display: none;
}

.menu-button svg {
  z-index: 4;
  width: 20px;
  height: 12px;
  position: relative;
}

.logo-header {
  z-index: 2;
  justify-content: center;
  align-items: center;
  display: flex;
}

.logo-header svg {
  width: 199px;
  height: 50px;
}

.header-section {
  background: #ffb8ca;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 0 20px;
  display: flex;
}

.js-menu-container {
  display: none;
}

.js-menu-container.is-open {
  width: 75vw;
  height: 100vh;
  z-index: 5;
  background-color: #e17992;
  flex-direction: column;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.js-close-menu {
  width: 14px;
  height: 14px;
  border: none;
  align-self: flex-end;
  display: flex;
}

.buy-btn-tablet {
  display: none;
}

.buy-btn {
  width: 100%;
  height: 44px;
  font-family: var(--font-family);
  color: #d41443;
  text-align: center;
  background: #fff;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 24px;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  display: flex;
}

.buy-btn:hover {
  background: #ecebeb;
}

.buy-btn svg {
  stroke-width: 2px;
  stroke: #d41443;
  width: 5px;
  height: 8px;
}

.menu-drawer-navigation a {
  color: #fff;
  font-family: var(--font-family);
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-decoration: none;
}

.menu-drawer-navigation li a {
  margin-bottom: 14px;
}

.current-nav a {
  color: #d41443;
}

.menu-drawer-navigation ul {
  flex-direction: column;
  margin-top: 33px;
  padding: 0;
