  background-color: #fff;
  border: none;
  border-radius: 22px;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.made-btn:hover {
  background-color: #ecebeb;
}

@keyframes animateBubble {
  0% {
    margin-top: 1000px;
  }

  100% {
    margin-top: -100%;
  }
}

@keyframes sideWays {
  0% {
    margin-left: 0;
  }

  100% {
    margin-left: 50px;
  }
}

.x1 {
  animation: animateBubble 25s linear infinite, sideWays 2s ease-in-out infinite alternate;
  top: 5%;
  left: -5%;
  transform: scale(.6);
}

.x2 {
  animation: animateBubble 20s linear infinite, sideWays 10s ease-in-out infinite alternate;
  top: 80%;
  left: 5%;
  transform: scale(.4);
}

.x3 {
  animation: animateBubble 28s linear infinite, sideWays 2s ease-in-out infinite alternate;
  top: 40%;
  left: 10%;
  transform: scale(.7);
}

.x4 {
  animation: animateBubble 22s linear infinite, sideWays 3s ease-in-out infinite alternate;
  top: 0;
  left: 20%;
  transform: scale(.3);
}

.x5 {
  animation: animateBubble 29s linear infinite, sideWays 4s ease-in-out infinite alternate;
  top: 50%;
  left: 30%;
  transform: scale(.5);
}

.x6 {
  animation: animateBubble 21s linear infinite, sideWays 2s ease-in-out infinite alternate;
  top: 0;
  left: 50%;
  transform: scale(.8);
}

.x7 {
  animation: animateBubble 20s linear infinite, sideWays 2s ease-in-out infinite alternate;
  top: 70%;
  left: 65%;
  transform: scale(.4);
}

.x8 {
  animation: animateBubble 22s linear infinite, sideWays 3s ease-in-out infinite alternate;
  top: 10%;
  left: 80%;
  transform: scale(.3);
}

.x9 {
  animation: animateBubble 29s linear infinite, sideWays 4s ease-in-out infinite alternate;
  top: 50%;
  left: 90%;
  transform: scale(.6);
}

.x10 {
  animation: animateBubble 26s linear infinite, sideWays 2s ease-in-out infinite alternate;
  top: 80%;
  left: 80%;
  transform: scale(.3);
}

.bubble {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  position: absolute;
  box-shadow: 0 20px 30px rgba(255, 255, 255, .2), inset 0 10px 30px 5px #fff;
}

.bubble:after {
  background: -moz-radial-gradient(center, ellipse cover, rgba(255, 255, 255, .5) 0%, rgba(255, 255, 255, 0) 70%);
  background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, rgba(255, 255, 255, .5)), color-stop(70%, rgba(255, 255, 255, 0)) );
  background: -webkit-radial-gradient(center, ellipse cover, rgba(255, 255, 255, .5) 0%, rgba(255, 255, 255, 0) 70%);
  background: -o-radial-gradient(center, ellipse cover, rgba(255, 255, 255, .5) 0%, rgba(255, 255, 255, 0) 70%);
  background: -ms-radial-gradient(center, ellipse cover, rgba(255, 255, 255, .5) 0%, rgba(255, 255, 255, 0) 70%);
  filter: progid:DXImageTransform. Microsoft. gradient(startColorstr= "#80ffffff", endColorstr= "#00ffffff", GradientType= 1);
  content: "";
  height: 180px;
  width: 180px;
  background: radial-gradient(rgba(255, 255, 255, .5) 0%, rgba(255, 255, 255, 0) 70%);
  border-radius: 50%;
  position: absolute;
  left: 10px;
  box-shadow: inset 0 20px 30px rgba(255, 255, 255, .3);
}

