/*how-its-made-section*/
.How-its-made-section {
  background-color: #fff5f6;
  min-width: 320px;
  padding: 60px 20px 60px 20px;
  background-image: url(/src/images/milk-img-1.png);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: 0% 80%;
}

.container-cow-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 32px;
}

.cow-title-1 {
  color: #ffa5ba;
  text-align: center;
  font-family: Titan One;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  margin-top: 0px;
  margin-bottom: 15px;
  transform: scale(1);
  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.cow-title-1:hover {
  transform: scale(1.2);
}

.cow-title-2 {
  color: #d41443;
  text-align: center;
  font-family: Titan One;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 1.36px;
  text-transform: uppercase;
  margin: 0px;
  transform: scale(1);
  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.cow-title-2:hover {
  transform: scale(1.2);
}

.cow-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cow-img {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-cow-cream {
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 380px;
}

.cow-cream-text-1 {
  min-width: 220px;
  flex-shrink: 0;
  color: #000;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 162%;
  letter-spacing: 0.48px;
  margin-top: 0px;
  margin-bottom: 18px;
  transform: scale(1);
  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.cow-cream-text-1:hover {
  transform: scale(1.2);
}

.cow-cream-text-2 {
  min-width: 220px;
  flex-shrink: 0;
  color: #907e82;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 162%;
  letter-spacing: 0.48px;
  margin-top: 0px;
  margin-bottom: 20px;
  transform: scale(1);
  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.cow-cream-text-2:hover {
  transform: scale(1.2);
}

.cow-button {
  width: 176px;
  height: 44px;
  flex-shrink: 0;
  border-radius: 22px;
  background: #fff;
  border: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #d41443;
  text-align: center;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  gap: 14px;
  transform: scale(1);
  transition: all 0.25s linear;
}

.cow-button:hover {
  transform: scale(1.2);
  background-color: rgba(236, 235, 235, 1);
  cursor: pointer;
}

.container-cow-cream-info ul {
  list-style: none;
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.icon-cow-1 {
  fill: rgba(255, 165, 186, 1);
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-5px);
  }
}

.icon-cow-1:hover,
.icon-cow-2:hover,
.icon-cow-3:hover {
  animation: bounce 0.3s ease infinite;
}

.icon-cow-2 {
  fill: rgba(255, 165, 186, 1);
}

.icon-cow-3 {
  fill: rgba(255, 165, 186, 1);
}

.icon-cow-amount {
  color: #d41443;
  font-family: Titan One;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 1.44px;
  text-transform: uppercase;
  margin-top: 16px;
  margin-bottom: 4px;
}

.icon-cow-amount-info {
  color: #000;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 170%;
  letter-spacing: 0.24px;
  flex-shrink: 0;
  margin-top: 0px;
  margin-bottom: 32px;
  max-width: 320px;
}

/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1157px) {
  /*how-it-s-made-section*/
  .How-its-made-section {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .cow-title-1 {
    font-size: 26px;
  }

  .cow-title-2 {
    font-size: 48px;
    letter-spacing: 1.92px;
  }

  .container-cow-title {
    margin-bottom: 40px;
  }

  .cow-container {
    flex-direction: row;
    gap: 40px;
    width: 768px;
  }

  .container-cow-cream {
    margin-top: 0px;
    margin-bottom: 0px;
  }

  .cow-cream-text-1 {
    font-size: 14px;
    line-height: 174%;
    letter-spacing: 0.56px;
    margin-bottom: 32px;
  }

  .cow-cream-text-2 {
    margin-bottom: 40px;
    font-size: 14px;
    line-height: 194%;
    letter-spacing: 0.56px;
  }

  .container-cow-cream-info ul {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    gap: 80px;
    margin-top: 52px;
    width: 180px;
  }

  .icon-cow-amount {
    font-size: 48px;
    letter-spacing: 1.92px;
  }

  .icon-cow-amount-info {
    font-size: 14px;
    line-height: 174%;
    letter-spacing: 0.28px;
    width: 170px;
    margin-top: 0px;
  }
}

/* Desktop */
@media screen and (min-width: 1158px) {
  /*how-its-made-section*/
  .How-its-made-section {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  #br-for-mobile {
    display: none;
  }

  .cow-title-1 {
    font-size: 30px;
    letter-spacing: 1.2px;
  }

  .cow-title-2 {
    font-size: 58px;
    letter-spacing: 3.48px;
  }

  .cow-container {
    gap: 80px;
    width: 1000px;
    display: flex;
    flex-direction: row;
  }

  .container-cow-cream {
    max-width: 419px;
  }

  .cow-cream-text-1 {
    font-size: 16px;
    line-height: 176%;
    letter-spacing: 0.64px;
    width: 419px;
  }

  .cow-cream-text-2 {
    width: 419px;
  }

  .icon-cow-amount {
    font-size: 48px;
    letter-spacing: 1.92px;
  }

  .icon-cow-amount-info {
    font-size: 16px;
    line-height: 176%;
    letter-spacing: 0.32px;
    width: 280px;
  }

  .container-cow-cream-info ul {
    gap: 80px;
    display: flex;
    flex-direction: row;
  }
}
